import React from "react";
import {
  LockOutlined,
  UserOutlined,
  CheckCircleOutlined,
  CloseOutlined,
  CloseCircleOutlined,
} from "@ant-design/icons";
import { Button, Form, Input, notification } from "antd";

import { validateEmail } from "../../helpers/validateEmail";
import { userStore } from "../../store/userStore";
import { postLogin } from "./postLogin";

import "./LoginForm.less";

export const LoginForm = (props) => {
  const onFinish = async (values) => {
    const isEmail = validateEmail(values.username);
    try {
      const loginRes = await postLogin(
        isEmail ? null : values.username,
        isEmail ? values.username : null,
        values.password
      );
      if (loginRes.access) {
        userStore.setIsAdmin(loginRes.access);
        notification.open({
          message: "Login was sucessfull!",
          placement: "topRight",
          className: "blackNotification",
          duration: 3,
          icon: <CheckCircleOutlined style={{ color: "green" }} />,
        });
        props.close(false);
      }
    } catch (e) {
      notification.error({
        message: e.response.data.result.error,
        placement: "topRight",
        className: "blackNotification",
        duration: 3,
        icon: <CloseCircleOutlined style={{ color: "red" }} />,
      });
    }
  };

  return (
    <div className="loginFormContainer">
      <div className="background" onClick={() => props.close(false)}></div>
      <div className="closeButton" onClick={() => props.close(false)}>
        <CloseOutlined />
      </div>
      <Form
        name="normal_login"
        className="loginForm"
        initialValues={{ remember: true }}
        onFinish={onFinish}
      >
        <Form.Item
          name="username"
          rules={[{ required: true, message: "Please input your Username!" }]}
        >
          <Input
            prefix={<UserOutlined className="site-form-item-icon" />}
            placeholder="Username"
          />
        </Form.Item>
        <Form.Item
          name="password"
          rules={[{ required: true, message: "Please input your Password!" }]}
        >
          <Input
            prefix={<LockOutlined className="site-form-item-icon" />}
            type="password"
            placeholder="Password"
          />
        </Form.Item>
        <Form.Item>
          <Button
            type="primary"
            htmlType="submit"
            className="login-form-button"
          >
            Log in
          </Button>
        </Form.Item>
        <a className="login-form-forgot" href="">
          Forgot password ?{/* TODO: forget password feature */}
        </a>
      </Form>
    </div>
  );
};
